import { Component, OnInit, Input, ElementRef } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { tipoARMA } from 'src/app/config/rpg.config';
import { AtaqueDTO } from 'src/app/models/fichas/utils/ataque.dto';
import { EquiparItemService } from 'src/app/services/gameplay/equipar-item.service';

@Component({
  selector: 'app-ataque-armas',
  templateUrl: './ataque-armas.component.html',
  styleUrls: ['./ataque-armas.component.scss'],
})
export class AtaqueArmasComponent implements OnInit {

  @Input('ataque')
  ataque: AtaqueDTO[] = [];

  isShow: boolean = true;
  icone: string = "arrow-round-down";

  constructor(public modalCtrl: ModalController,
    private equiparService: EquiparItemService) {

  }
  ngOnInit() {
    this.ataque = this.equiparService.ataquesEquipados;
  }

  show(info) {
    if (this.isShow) {
      info.style.display = "block";
      this.icone = "arrow-round-up";
      this.isShow = false;
    } else {
      info.style.display = "none";
      this.icone = "arrow-round-down";
      this.isShow = true;
    }

  }



  getTipo(item): string {
    let tipo = "";
    switch (item) {
      case tipoARMA.Corte:
        tipo = "Corte";
        break;


      case tipoARMA.Esmagamento:
        tipo = "Esmagamento";
        break;


      case tipoARMA.Perfuacao:
        tipo = "Perfuração";
        break;
    }

    return tipo;
  }

  close() {
    this.modalCtrl.dismiss();
  }

}
