import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { FichaUpdateDTO } from 'src/app/models/fichas/ficha-update.dto';
import { FichaNewDTO } from 'src/app/models/fichas/ficha_new.dto';
import { FichaInsertDTO } from 'src/app/models/fichas/ficha-insert.dto';

@Injectable({
  providedIn: 'root'
})
export class FichaService {

  ficha = undefined;
  isJogador = true;
  idNpc: string;
  idMesa: string;
  tipoNpc: string;
  closeBTN = false;

  constructor(private http: HttpClient) { }

  getConfiguration() {
    return {
      ficha: this.ficha,
      isJogador: this.isJogador,
      idNpc: this.idNpc,
      idMesa: this.idMesa,
      tipoNpc: this.tipoNpc,
      closeBTN: this.closeBTN
    }
  }

  setConfiguration(ficha, isJogador, idNpc, idMesa, tipoNpc, closeBTN) {
    this.ficha = ficha;
    this.isJogador = isJogador;
    this.idNpc = idNpc;
    this.idMesa = idMesa;
    this.tipoNpc = tipoNpc;
    this.closeBTN = closeBTN;
  }

  resetConfiguration() {
    this.ficha = undefined;
    this.isJogador = true;
    this.idNpc = undefined;
    this.idMesa = undefined;
    this.tipoNpc = undefined;
    this.closeBTN = false;
  }

  findFichaById(id: string): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}ficha/${id}`,
      {
        responseType: 'json',
        observe: "body"
      });
  }

  update(dto: FichaUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}ficha/`,
      dto)
  }

  updateFichaNpc(dto: FichaUpdateDTO, idNpc: string, idMesa: string): Observable<any> {
    const params = new HttpParams()
      .set('idNpc', idNpc)
      .set('idMesa', idMesa);
    return this.http.put(`${API_CONFIG.baseUrl}mesa/update/npc/`,
      dto,
      {
        params
      }
    );
  }

  findFichaByMesaAndJogador(idUser: string, idMesa: string): Observable<any> {
    const params = new HttpParams()
      .set('idUser', idUser)
      .set('idMesa', idMesa);

    return this.http.get(`${API_CONFIG.baseUrl}ficha/mesa`,
      {
        responseType: 'json',
        observe: "body",
        params
      });
  }

  insert = (dto: FichaInsertDTO): Observable<any> => {
    return this.http.post(`${API_CONFIG.baseUrl}ficha`,
      dto,
      {
        responseType: 'json',
      }

    );
  }

  updateImg(idFicha: string, urlImg: string): Observable<any> {
    const params = new HttpParams()
      .set('idFicha', idFicha)
      .set('urlImg', urlImg);

    return this.http.put(`${API_CONFIG.baseUrl}ficha/imagem`,
      null,
      {
        responseType: 'json',
        observe: "body",
        params
      });
  }

  updateImgNpc(idNpc: string, idMesa: string, urlImg: string): Observable<any> {
    const params = new HttpParams()
      .set('idNpc', idNpc)
      .set('idMesa', idMesa)
      .set('urlImg', urlImg);

    return this.http.put(`${API_CONFIG.baseUrl}mesa/update/npc/img`,
      null,
      {
        responseType: 'json',
        observe: "body",
        params
      });
  }



}
