import { Injectable } from '@angular/core';
import { HttpClient, HttpHandler, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { AtaqueDTO } from 'src/app/models/fichas/utils/ataque.dto';
import { API_CONFIG } from 'src/app/config/api.config';
import { DefesaDTO } from 'src/app/models/fichas/utils/defesa.dto';
import { UtilizavelDTO } from 'src/app/models/fichas/utils/utilizavel.dto';
import { StorageService } from '../../storage-service.service';
import { ItemAtaqueUpdateDTO } from 'src/app/models/item/item-ataque-update.dto';
import { ItemDefesaUpdateDTO } from 'src/app/models/item/item-defesa-update.dto';
import { ItemUtilizavelUpdateDTO } from 'src/app/models/item/item-utilizavel-update.dto';

@Injectable({
  providedIn: 'root'
})
export class ItemService {

  private staticID: string;
  private staticType: number;

  public getStaticID(): string {
    return this.staticID;
  }

  public setStaticID(id: string): void {
    this.staticID = id;
  }

  public getStaticType(): number {
    /* 
    0 - Ataque;
    1 - Defesa;
    2 - Utilizavel;
    */
    return this.staticType;
  }

  public setStaticType(type: number): void {
    /* 
    0 - Ataque;
    1 - Defesa;
    2 - Utilizavel;
    */
    this.staticType = type;
  }

  constructor(private http: HttpClient,
    private storage: StorageService) { }

  findPageOfAtaque(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}item/ataque/page`,
      {
        params,
        observe: 'body',
      });
  }

  findPageOfDefesa(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}item/defesa/page`,
      {
        params,
        observe: 'body',
      });
  }

  findPageOfUtilizavel(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}item/utilizavel/page`,
      {
        params,
        observe: 'body',
      });
  }

  insertAtaque(obj: AtaqueDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}item/ataque`,
      obj,
      {
        observe: 'response'
      }
    );
  }

  insertDefesa(obj: DefesaDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}item/defesa`,
      obj,
      {
        observe: 'response'
      }
    );
  }

  insertUtilizavel(obj: UtilizavelDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}item/utilizavel`,
      obj,
      {
        observe: 'response'
      }
    );
  }

  findById(idItem: string, tipoItem: string): Observable<any> {
    const params = new HttpParams()
      .set('tipoItem', tipoItem);

    return this.http.get(`${API_CONFIG.baseUrl}item/${idItem}`,
      {
        params,
        observe: 'body',
      });
  }

  updateAtaque(dto: ItemAtaqueUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}item`,
      dto);
  }

  updateDefesa(dto: ItemDefesaUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}item`,
      dto);
  }

  updateUtilizavel(dto: ItemUtilizavelUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}item`,
      dto);
  }


}
