import { Component, OnInit, Input } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { EspecializacaoDTO } from 'src/app/models/fichas/utils/especializacao.dto';
import { EspecializacaoService } from 'src/app/services/especializacao/especializacao.service';
import { EspecializacaoNewDTO } from 'src/app/models/fichas/utils/especializacao-new.dto';
import { stringify } from 'querystring';

@Component({
  selector: 'especializacoes',
  templateUrl: './especializacoes.component.html',
  styleUrls: ['./especializacoes.component.scss'],
})
export class EspecializacoesComponent implements OnInit {

  @Input("especializacoes")
  especializacoes: EspecializacaoDTO[] = [];

  @Input("closeButton")
  closeButton: boolean = false;

  @Input("idFicha")
  idFicha = '';

  @Input("idAtributo")
  idAtributo = '';

  constructor(public alertController: AlertController, public modalCtrl: ModalController, private especService: EspecializacaoService) { }

  ngOnInit() {
  }

  addEspec() {
    this.presentAlertPrompt()
  }

  fav(especializacao, slide) {
    const aux = !especializacao.isFav
    this.especService.setFav(especializacao.idEspecializacao, aux.toString())
      .subscribe(res => { especializacao.isFav = aux })

    slide.close();
  }

  remov(especializacao) {
    this.especializacoes.forEach((element, index, arr) => {
      if (element.nome == especializacao.nome) {
        arr.splice(index, 1)
      }
    })

    this.especService.delete(this.idFicha, this.idAtributo, especializacao.idEspecializacao)
      .subscribe(res => this.especializacoes.splice(this.especializacoes.indexOf(especializacao), 1));
  }

  async presentAlertPrompt() {
    const alert = await this.alertController.create({
      header: 'Adicionar',
      inputs: [
        {
          name: 'nome',
          type: 'text',
          placeholder: 'Nome da especialização'
        },
        {
          name: 'desc',
          type: 'text',
          placeholder: 'Descrição da especialização'
        }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
          }
        }, {
          text: 'Salvar',
          handler: data => {
            let espec: EspecializacaoNewDTO = {
              nome: data.nome,
              descricao: data.desc
            }
            this.especService.insert(espec, this.idFicha, this.idAtributo).
              subscribe(res => {
                this.especializacoes.push({
                  idEspecializacao: res.idEspecializacao,
                  nome: res.nome,
                  descricao: res.descricao,
                  isFav: res.isFav
                });
              });
          }
        }
      ]
    });

    await alert.present();
  }

}
