import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { MessageService } from 'primeng/api';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';

@Component({
  selector: 'app-imagem',
  templateUrl: './imagem.component.html',
  styleUrls: ['./imagem.component.scss'],
})
export class ImagemComponent implements OnInit {

  src: string = '';
  defaultImg: string = 'assets/imgs/dice.png';

  constructor(public modalCtrl: ModalController,
    private service: PaginacaoService,
    private toast: MessageService) {
    this.src = this.src || this.defaultImg;
  }

  ngOnInit() {
    const p = this.service.getPromisse();
    if (p) {
      this.src = p;
    }
  }

  close() {
    this.modalCtrl.dismiss(this.src);
  }

  getImg() {
    if (this.src.length < 5 || !this.src) {
      return this.defaultImg;
    } else {
      return this.src;
    }
  }

  update() {
    const p = this.service.getPromisse();
    p.subscribe(res => console.log(res));
  }

}
